// –°—Ç–∞—Ä—Ç–æ–≤—ã–µ (–¥–µ–º–æ) –∫—É—Ä—Å—ã ‚Äì –ø–æ—Ç–æ–º –≤—ã –∏—Ö –º–µ–Ω—è–µ—Ç–µ —Ä—É–∫–∞–º–∏ –≤ –∞–¥–º–∏–Ω-–ø–∞–Ω–µ–ª–∏
const DEFAULT_RATES: Record<string, number> = {
  "BTC-PLN": 160000,
  "BTC-EUR": 36000,
  "BTC-USD": 39000,
  "ETH-PLN": 8000,
  "ETH-EUR": 1800,
  "ETH-USD": 2000,
  "USDT-PLN": 4.2,
  "USDT-EUR": 0.9,
  "USDT-USD": 1.0, // 1 USDT = 1 USD –ø–æ —É–º–æ–ª—á–∞–Ω–∏—é
};

type RatesMap = Record<string, number>;

type CommissionInfo = {
  kind: "flat" | "none" | "bonus";
  percent: number;
  value: number; // –≤ —Ü–µ–ª–µ–≤–æ–π —Ñ–∏–∞—Ç-–≤–∞–ª—é—Ç–µ
};

type CalcResult = {
  gross: number;
  net: number;
  commission: CommissionInfo;
  rate: number;
  grossUsdt: number;
};

// ===============================
//  –¢–í–û–ò –ü–†–ê–í–ò–õ–ê –ö–û–ú–ò–°–°–ò–ò (USDT)
// ===============================
//
// –¥–æ 1499 USDT   ‚Üí –∫–æ–º–∏—Å—Å–∏—è 15 USDT —Å –∫–ª–∏–µ–Ω—Ç–∞
// 1500 ‚Äì 5000    ‚Üí –±–µ–∑ –∫–æ–º–∏—Å—Å–∏–∏
// —Å–≤—ã—à–µ 5000     ‚Üí –≤—ã –¥–æ–ø–ª–∞—á–∏–≤–∞–µ—Ç–µ –∫–ª–∏–µ–Ω—Ç—É +1% (–±–æ–Ω—É—Å)
//
function computeCommission(
  grossFiat: number,
  toFiat: string,
  rates: RatesMap
): CommissionInfo {
  const usdtKey = `USDT-${toFiat}`;
  const usdtRate = rates[usdtKey] ?? DEFAULT_RATES[usdtKey];

  const grossUsdt = grossFiat / usdtRate;

  if (grossUsdt <= 1499) {
    const commissionFiat = 15 * usdtRate;
    const percent = (commissionFiat / grossFiat) * 100;
    return { kind: "flat", percent, value: commissionFiat };
  }

  if (grossUsdt > 1499 && grossUsdt <= 5000) {
    return { kind: "none", percent: 0, value: 0 };
  }

  const bonusFiat = grossFiat * 0.01;
  return { kind: "bonus", percent: 1, value: bonusFiat };
}

