  // –õ–æ–∫–∞–ª—å–Ω–æ–µ —Å–æ—Å—Ç–æ—è–Ω–∏–µ –¥–ª—è —Ä—É—á–Ω–æ–π –ø—Ä–∞–≤–∫–∏ –∫—É—Ä—Å–∞
